<script setup>
import { ref } from 'vue'
import { useAuthStore } from '../../../../store/modules/auth'

const authStore = useAuthStore()
const form = ref({
  email: ''
})

const handleSubmit = async () => {
  try {
    await authStore.resetPassword(form.value.email)
  } catch (error) {
    console.error('Password reset failed:', error)
  }
}
</script>